import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap, Router, NavigationEnd, NavigationStart, Event } from '@angular/router';
import { Location, PopStateEvent } from "@angular/common";
import { map, switchMap } from 'rxjs/operators';
import { ConfiguradorDWService } from './services/configurador-dw.service';
import { ConfiguradorDW } from './models/configurador-dw.model';
import { Observable, BehaviorSubject, of } from 'rxjs';
import { faTimes } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'lib-root',
  templateUrl: 'configurador-dw.component.html',
  styleUrls: ['./configurador-dw.component.scss']
})
//Adequar o nome da classe de acordo com a nova tela
export class ConfiguradorDWComponent implements OnInit{

  linkList: string[] = [];
  private yScrollStack: number[] = [];
  private goBackLink: string;
  openType:string = "";
  resourceId:string = "";
  faTimes = faTimes;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private location: Location,
              private service: ConfiguradorDWService) {
                this.goBackLink = '/'+ this.service.getResourceName();
  }

  ngOnInit() {
    this.service.loadListar();
    this.route.params.subscribe(params => {
      console.log(params)
    if (params.hasOwnProperty("openType"))  
      this.openType = params.get("openType");
    if (params.hasOwnProperty("code"))  
      this.resourceId = params.get("code");
    });
    /*this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
    });*/
    const scrollLevels: { [navigationId: number]: number } = {};
    let lastId = 0;
    let restoredId: number;

    this.router.events.subscribe((ev: Event) => {

      if (ev instanceof NavigationStart) {
        scrollLevels[lastId] = window.scrollY;
        lastId = ev.id;
        this.linkList.push(ev.url);
        restoredId = ev.restoredState ? ev.restoredState.navigationId : undefined;
      }

      if (ev instanceof NavigationEnd) {
        if (restoredId) {
          // Optional: Wrap a timeout around the next line to wait for
          // the component to finish loading
          
          this.goBackLink = this.linkList.pop();
          window.scrollTo(0, scrollLevels[restoredId] || 0);
        } else {
          window.scrollTo(0, 0);
        }
      }

    });

  }

  ngOnDestroy(){
    this.service.limpaDados();
  }

  goback(){
    this.router.navigate([this.goBackLink]);
  }

  //get routeList$() { return this._routeList$.asObservable(); }
  /*getAnimationData(outlet: RouterOutlet) {
    return outlet && outlet.activatedRouteData; //&& outlet.activatedRouteData['animation'];
  }*/

  
}