import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import {ConfiguradorDWComponent } from './configurador-dw.component';
import {ConfiguradorDWListComponent } from './list/configurador-dw-list.component';
import {ConfiguradorDWEditComponent } from './editor/configurador-dw-edit.component';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: ConfiguradorDWComponent,
          children: [
            {path: '', component: ConfiguradorDWListComponent},
            {path: ':openType',component: ConfiguradorDWEditComponent},
            {path: ':openType/:code',component: ConfiguradorDWEditComponent }
          ]
        }
    ])
  ],
  exports: [RouterModule]
})
export class ConfiguradorDWRoutingModule { }