import { Injectable } from '@angular/core';
import { NgbDateStruct, NgbDateParserFormatter, NgbDate } from '@ng-bootstrap/ng-bootstrap';

@Injectable({
    providedIn: 'root'
  })
export class BrDateFormatter extends NgbDateParserFormatter {
    parse(value: string): NgbDateStruct {
        if (value == null)
            return null;
        //console.log(value);
        let values = value.split('/');
        const date = new NgbDate(Number(values[2]), Number(values[1]), Number(values[0]));
        return date;
    }
    format(date: NgbDateStruct): string {
        if (date == null)
            return null;
        //console.log(`${date.day}/${date.month}/${date.year}`);
        return `${date.day}/${date.month}/${date.year}`;
    }
}