import { Component, OnInit, Input, QueryList, ViewChildren } from '@angular/core';

import { DecimalPipe } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

import { Observable } from 'rxjs';

import { ConfiguradorDWService } from '../services/configurador-dw.service';

import { ConfiguradorDW } from '../models/configurador-dw.model';

import { NgbdSortableHeader, SortEvent } from '../directives/sortable.directive';
import { faSort, faPlus, faTrashAlt, faPencilAlt, faSearch, faTimesCircle, faPaperPlane } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'configurador-dw-list',
  templateUrl: './configurador-dw-list.component.html',
  styleUrls: ['./configurador-dw-list.component.scss'],
  providers: [DecimalPipe]
})
export class ConfiguradorDWListComponent implements OnInit {
  title = '';
  resourceName = '/';
  faPlus = faPlus;
  faPencilAlt = faPencilAlt;
  faTrashAlt = faTrashAlt;
  faSearch = faSearch;
  faSort = faSort;
  loading$: Observable<boolean>;
  arrayConfiguradorDW$: Observable<ConfiguradorDW[]>;
  total$: Observable<number>;

  selectedId: string = "";
  static LOCALE_KEY = 'currentLocale';
  

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;

  constructor(
    private route: ActivatedRoute,
    private router: Router,              
    private service: ConfiguradorDWService,
    public translate: TranslateService
  ) {
    this.arrayConfiguradorDW$ = service.items$;
    this.total$ = service.total$;
    this.loading$ = service.loading$;
    this.resourceName = '/'+ service.getResourceName();

    translate.setDefaultLang('pt');
    const currentLocale =  sessionStorage.getItem(ConfiguradorDWListComponent.LOCALE_KEY);
    
    translate.setDefaultLang(currentLocale);
  }

  get page() { return this.service.page; }
  get pageSize() { return this.service.pageSize; }
  set page(page: number) { this.service.page =  page;}
  set pageSize(pageSize: number) { this.service.pageSize =  pageSize; }

  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting items
    this.service.sortColumn = column;
    this.service.sortDirection = direction;
  }

  ngOnInit() {
   
  }

  goToManagePage(itemConfigDW: any): void {
    if(itemConfigDW)
      this.router.navigate([this.resourceName, 'manage', itemConfigDW.nomeTabela]);
    else
      this.router.navigate([this.resourceName, 'manage']);
  }
}