import { ActivatedRoute, Router, NavigationStart } from '@angular/router';
import { Component, OnInit } from '@angular/core';

import { NgbDateStruct, NgbDate, NgbDatepickerI18n, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';

import { Observable, of } from 'rxjs';

import { LovValsService } from '../services/lovvals.service';

import { ConfiguradorDW } from '../models/configurador-dw.model';
import { LovVals } from '../models/lovvals.model';
import { faCalendarAlt, faSort, faTrashAlt, faPlus } from '@fortawesome/free-solid-svg-icons';

import { I18n, CustomDatepicker } from '../util/CustomDatepicker';
import { BrDateFormatter } from '../util/DateFormatters';
import { ConfiguradorDWService } from '../services/configurador-dw.service';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { DecimalPipe } from '@angular/common';
import { Coluna } from '../models/coluna.model';
import { filter, map } from 'rxjs/operators';


@Component({
	selector: 'configurador-dw-edit',
	templateUrl: './configurador-dw-edit.component.html',
	styleUrls: ['./configurador-dw-edit.component.scss'],
	providers: [
		I18n, { provide: NgbDatepickerI18n, useClass: CustomDatepicker },
		{ provide: NgbDateParserFormatter, useClass: BrDateFormatter },
		LovValsService, DecimalPipe]
})
export class ConfiguradorDWEditComponent implements OnInit {
	subtitle: string;
	resourceId: string;
	openType: string;
	faSort = faSort;
	faTrashAlt = faTrashAlt;
	faPlus = faPlus;
	faCalendarAlt = faCalendarAlt;

	addFiltrerEnabled: boolean = false;
	goBackLink: string;
	content: ConfiguradorDW;
	originalCopy: ConfiguradorDW;
	listaColunasTabelaSelecionada: Array<Coluna> = [];

	mapAcao = {
		'I': 'Insert',
		'A': 'Update',
		'E': 'Delete'
	}

	errors: string;
	private state$: Observable<object>;

	tiposComissao$: Observable<any[]>;
	static LOCALE_KEY = 'currentLocale';
	public nomeTabelaSelecionada = "";
	public configuradorDwSelecionado: ConfiguradorDW = new ConfiguradorDW();
	arrayConfiguradorDW$: Observable<ConfiguradorDW[]>;

	public RADIO_AUDIT_LOG_ENABLED = true;
	public RADIO_ALL_ENABLED = true;
	public RADIO_AUDIT_LOG_VALUE = 'AUDIT_LOG';
	public RADIO_TABELA_VALUE = 'TABELA';
	public RADIO_NENHUM_VALUE = 'NENEHUM';

	public RADIO_ACAO_ALTERACAO = 'A';
	public RADIO_ACAO_INCLUSAO = 'I';
	public RADIO_ACAO_EXCLUSAO = 'E';

	public radioInsert: any;
	public radioUpdate: any;
	public radioDelete: any;
	public radioAddFIltro: any;

	private TiposAceitaveisColuna = ['TIMESTAMP', 'DATE']

	cboxTabelaInteira: boolean = true;

	constructor(
		private route: ActivatedRoute,
		private router: Router,
		private ConfiguradorDWService: ConfiguradorDWService,
		private toastr: ToastrService,
		public translate: TranslateService
	) {
		this.arrayConfiguradorDW$ = ConfiguradorDWService.items$;

		translate.setDefaultLang('pt');
		const currentLocale = sessionStorage.getItem(ConfiguradorDWEditComponent.LOCALE_KEY);
		translate.setDefaultLang(currentLocale);

		this.content = new ConfiguradorDW();
	}

	ngOnInit() {
		this.nomeTabelaSelecionada = this.route.snapshot.paramMap.get('code');

		this.goBackLink = '/' + this.ConfiguradorDWService.getResourceName();
		this.setTableContent(this.nomeTabelaSelecionada);
	}

	setTableContent(tabelaNome): void {
		this.content = new ConfiguradorDW();

		this.ConfiguradorDWService.selectItem(tabelaNome).subscribe(item => {
			this.content = (item) ? item : this.content;
			this.originalCopy = JSON.parse(JSON.stringify(this.content));
			console.log('Nova copia criada ', this.content)
			
			this.changeCheckboxTabelaInteira(this.content.formaEnvio === 'T')
			this.setupRadioButtons(this.content.formaEnvio);
		});		
	}

	ChangeSelectedTable(): void {
		console.log('Retornando copia', this.content)
		Object.assign(this.content, this.originalCopy)
		console.log('copia restaurada', this.content)

		this.closeAddFiltroCard();
		this.setTableContent(this.nomeTabelaSelecionada);
	}

	public changeCheckboxTabelaInteira(formaEnvioT: boolean) {
		this.cboxTabelaInteira = formaEnvioT;
		//let valueForm = value ? "T" : "E";
		this.content.formaEnvio = formaEnvioT ? 'T' : this.content.formaEnvio;
		this.setupRadioButtons(this.content.formaEnvio);
	}

	private setupRadioButtons(formaEnvio: string): void {
		if (formaEnvio !== "T" && (this.content.nomeTabela)) {
			this.radioInsert = this.RADIO_NENHUM_VALUE;
			this.radioUpdate = this.RADIO_NENHUM_VALUE;
			this.radioDelete = this.RADIO_NENHUM_VALUE;

			this.setRadioAllStatus(true);
			let logDisabled = ["DELQMST", "UDA"];
			if (logDisabled.some(item => this.content.nomeTabela.includes(item))) {
				//log radio disabled
				this.RADIO_AUDIT_LOG_ENABLED = false;
			}

			if (formaEnvio === "R") {
				this.radioInsert = this.logicaRadioButtons('envioInsert');
				this.radioUpdate = this.logicaRadioButtons('envioUpdate');
				this.radioDelete = this.logicaRadioButtons('envioDelete');
			}
		}
		else if (formaEnvio === "T") {
			//NENHUM radio habilitado
			this.setRadioAllStatus(false);
		}
	}

	private setRadioAllStatus(status: boolean): void {
		this.RADIO_ALL_ENABLED = status;
	}

	//pass data by reference
	public logicaRadioButtons(tipoEnvio): string {

		switch (this.content[tipoEnvio]) {
			case this.content.nomeTabela:
				if (tipoEnvio !== 'envioDelete') {
					return this.RADIO_TABELA_VALUE
				}
				break;
			case this.RADIO_AUDIT_LOG_VALUE:
				return this.RADIO_AUDIT_LOG_VALUE;
				break;
			default:				
				return this.RADIO_NENHUM_VALUE;
				break;
		}
	}

	/*Nao precisa de funÃ§Ã£o; Usando NgModel
	changeInsertRadio(): void {}
	changeUpdateRadio(): void {}
	changeDeleteRadio(): void {}
	*/

	save() {
		if (this.cboxTabelaInteira) {
			this.content.envioDelete = null;
			this.content.envioInsert = null;
			this.content.envioUpdate = null;
		} else if(
			this.radioDelete === this.RADIO_NENHUM_VALUE &&
			this.radioInsert === this.RADIO_NENHUM_VALUE &&
			this.radioUpdate === this.RADIO_NENHUM_VALUE
		){
			this.content.formaEnvio = 'E';
			this.content.envioDelete = null;
			this.content.envioInsert = null;
			this.content.envioUpdate = null;
		}else {
			this.content.formaEnvio = 'R';
			if(this.radioInsert === this.RADIO_TABELA_VALUE)
				this.content.envioInsert = this.content.nomeTabela;
			else if(this.radioInsert === this.RADIO_AUDIT_LOG_VALUE)
				this.content.envioInsert = this.RADIO_AUDIT_LOG_VALUE;

			if(this.radioUpdate === this.RADIO_TABELA_VALUE)
				this.content.envioUpdate = this.content.nomeTabela;
			else if(this.radioUpdate === this.RADIO_AUDIT_LOG_VALUE)
				this.content.envioUpdate = this.RADIO_AUDIT_LOG_VALUE;

			if(this.radioDelete === this.RADIO_TABELA_VALUE)
				this.content.envioDelete = this.content.nomeTabela;
			else if(this.radioDelete === this.RADIO_AUDIT_LOG_VALUE)
				this.content.envioDelete = this.RADIO_AUDIT_LOG_VALUE;
		}

		this.ConfiguradorDWService.save(this.content).subscribe(
			(resp) => {
				console.log(resp);
				this.goListPage()
			},
			(error) => {
				//this.content = this.originalCopy;
			});
	}

	public removeFilter(indice: number): void {
		this.content.filtros.splice(indice, 1);
	}


	cancel() {
		this.content = this.originalCopy;
		this.goListPage();
	}

	private goListPage() {
		this.router.navigate([this.goBackLink]);
	}


	// ================= Card CRUD Filtro Acao ==================== //

	public openAddFilter(): void {
		this.addFiltrerEnabled = true;
		this.radioAddFIltro = this.RADIO_ACAO_INCLUSAO;

		//TODO: chamada lovvals das colunas date e timestramps da tabela.
		this.getColunasByTabela(this.nomeTabelaSelecionada);
	}

	public closeAddFiltroCard() {
		this.addFiltrerEnabled = false;
		this.listaColunasTabelaSelecionada = [];
	}

	public addFiltro(nomeCampo: string): void {
		//TODO: validar se nao ja foi adicionado 		

		let existeFiltro = this.content.filtros.some(item => item.acao === this.radioAddFIltro && item.nomeCampo === nomeCampo);
		if (existeFiltro) {
			this.toastr.error('Filtro jÃ¡ adicionado!', 'CyberMessage');
			return;
		}

		this.content.filtros.push({
			acao: this.radioAddFIltro,
			nomeCampo: nomeCampo
		})
		this.addFiltrerEnabled = false;
	}

	private getColunasByTabela(tabelaNome: string):void {
		this.ConfiguradorDWService.getListaColunas(tabelaNome).subscribe(
			(res: Array<Coluna>) => {
				this.listaColunasTabelaSelecionada = [...res].filter(col => this.TiposAceitaveisColuna.includes(col.dataType));
		})
	}
}