import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { SearchResult } from '../models/search-result.model';
import { State } from '../models/state.model';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { catchError, debounceTime, delay, switchMap, tap } from 'rxjs/operators';

import { LovVals } from '../models/lovvals.model';
import { SortDirection } from '../directives/sortable.directive';
import { HttpErrorHandler, HandleError } from './http-error-handler.service';
import { ToastrService } from 'ngx-toastr';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort<T>(items: T[], column: string, direction: string): T[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(valores: any, id: string, text: string) {
  const term = text.toLowerCase();
  let item: LovVals = valores as LovVals;
  return item.id.includes(id)
    && (item.codigo.toLowerCase().includes(term));
}


@Injectable({
  providedIn: 'root'
})
export class LovValsService<T>{
  //private httpUrl = '';
  private serviceUrl = '/conppwPadraoC9-war/ppwpadrao/v9/lovvals';
  private handleError: HandleError;

  private listaValores: T[] = [];
  private _loading$ = new BehaviorSubject<boolean>(true);
  private _search$ = new Subject<void>();
  private _valores$ = new BehaviorSubject<T[]>([]);
  private _total$ = new BehaviorSubject<number>(0);
  private _serviceName = "LovValsService";

  private _state: State = {
    page: 1,
    pageSize: 10,
    codigo: '',
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  constructor(private http: HttpClient,
    private httpErrorHandler: HttpErrorHandler,
    private toastr: ToastrService) {
      this.handleError = httpErrorHandler.createHandleError(this._serviceName);
      this.getItem("TPPGTO").subscribe(
        items => {
          this.listaValores = items;
          this._valores$.next(this.listaValores);
        }, 
        error => console.info (error) // error path
        );
      this._search$.pipe(
        tap(() => this._loading$.next(true)),
        debounceTime(200),
        switchMap(() => this._search()),
        delay(200),
        tap(() => this._loading$.next(false))
      ).subscribe(result => {
        this._valores$.next(result.items);
        this._total$.next(result.total);
      });
      this._search$.next();
  }

  get valores$() { return this._valores$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }

  private _search(): Observable<SearchResult<T>> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm, codigo } = this._state;

    // 1. sort
    let items = sort(this.listaValores, sortColumn, sortDirection);

    // 2. filter
    items = items.filter(item => matches(item, codigo, searchTerm));
    const total = items.length;

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }

  getResourceName():string{
    return 'configuradordw';
  }
	/*private log(message: string) {
	   this.messageService.add("info",` ${this._serviceName}: ${message}`);
     console.log(message);
	}*/

  getItems(): Observable<T[]> {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
  return this.http.get<T[]>(`${this.serviceUrl}`)
      .pipe(
        tap(_ => console.log(`fetched []${_.length}] items `)),
        catchError(this.handleError<T[]>('Carregar Lista de valores', []))
      );

  }

  getItem(codigoItem: string): Observable<T[]> {
    return this.http.get<T[]>(`${this.serviceUrl}/${codigoItem}`)
      .pipe(
        tap(
          _ => {
            console.log(`${this._serviceName}: Item selecionado com cï¿½digo=${codigoItem}`)
        }),
        catchError(this.handleError<T[]>(`Buscar Lista cï¿½digo=${codigoItem}`))
      );
  }

  putItem(item: T, keyName: string): Observable<any> {
    //return this.http.put(`${this.serviceUrl}/${item[keyName]}`, item, httpOptions).pipe(
    return this.http.put(`${this.serviceUrl}`, item, httpOptions).pipe(
      tap(_ => console.log(`${this._serviceName}: Item cï¿½digo=${item[keyName]} atualizada com sucesso!`)),
      catchError(this.handleError<any>(`updateItem cï¿½digo=${item[keyName]}`))
    );
  }

  postItems(items: T[]): Observable<T[]> {
    return this.http.post<T[]>(this.serviceUrl, items, httpOptions).pipe(
      tap((newItems: T[]) => console.log(`${this._serviceName}: Foram atualizadas ${newItems.length} items com sucesso!`)),
      catchError(this.handleError<T[]>(`postItems cï¿½digo=${items.length}`))
    );
  }

  /** DELETE: delete item from the server */
  deleteItem(item: T | number, keyName?: string): Observable<T> {
    const id = typeof item === 'number' ? item : item[keyName];
    return this.http.delete<T>(`${this.serviceUrl}/${id}`, httpOptions).pipe(
      tap(_ => console.log(`item removido com cï¿½digo=${id}`)),
      catchError(this.handleError<T>('deleteItem'))
    );
  }

  searchItem(term: string, codigo: string): Observable<T[]> {
    if (!term.trim()) {
      // if not search term, return empty hero array.
      return this.getItem(codigo);
    }
    //const url = `${this.httpUrl}/${this.serviceUrl}/?term=${term}`;
    const url = `${this.serviceUrl}/${codigo}/${term}`;
    return this.http.get<T[]>(url).pipe(
      tap(_ => console.log(_)),
      catchError(this.handleError<T[]>('searchItem', []))
    );
    //tap(_ =>console.log(`found items matching "${term}"`)),
  }
}